import React, { useEffect, useState } from 'react'
import Modal from 'react-bootstrap/Modal'
import { Badge, Button, Col, Container, Form, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { message } from 'antd'
import axios from 'axios'
import { setUser } from '../../features/userSlice'
import StatusBadge from '../../components/StatusBadge'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [show, setShow] = useState(false)
  const [couponQty, setCouponQty] = useState(0)
  const [totalAmount, setTotalAmount] = useState(0)
  const [couponPurchaseHistory, setCouponPurchaseHistory] = useState([])
  const dispatch = useDispatch()

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)

  const formattedDate = (date) => {
    const dateObject = new Date(date)
    const format = `${dateObject.getFullYear()}-${String(dateObject.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(dateObject.getDate()).padStart(2, '0')} ${String(dateObject.getHours()).padStart(
      2,
      '0',
    )}:${String(dateObject.getMinutes()).padStart(2, '0')}:${String(
      dateObject.getSeconds(),
    ).padStart(2, '0')}`

    return format
  }
  const handleInputChange = (e) => {
    // Get the input value
    const inputValue = e.target.value;

    // Check if the input is a valid integer
    if (/^-?\d*$/.test(inputValue)) {
      // Update the state with the valid integer value
      setCouponQty(inputValue);
      setTotalAmount(inputValue * user.couponPrice)
    }
    // If the input is not a valid integer, do nothing (don't update the state)
  };
  const handlePurchase = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      if (couponQty === '' || couponQty === 0) {
        setShow(false)
        return message.error('Coupon quantity must be greater than 0')
      }
      if (couponQty > user.walletBalance / user.couponPrice) {
        setShow(false)
        return message.error('Insufficient wallet balance')
      }

      let amount = couponQty * user.couponPrice
      const data = {
        userId: user.uniqueId,
        singleCouponPrice: user.couponPrice,
        quantity: couponQty,
      }
      const res = await axios.post('/api/v1/retailer/initiateCouponPurchase', data)
      if (res.data.success) {
        message.success('Coupon purchase request sent successfully')
        dispatch(setUser({ ...user, walletBalance: user.walletBalance - amount }))
        getCouponPurchaseHistory()
        setShow(false)
      } else {
        setShow(false)
      }
    } catch (error) {
      console.log(error)
    }
  }

  const getCouponPurchaseHistory = async () => {
    try {
      const userId = user._id
      const res = await axios.post('/api/v1/retailer/getPurchaseCouponHistory', { userId })
      if (res.data.success) {
        setCouponPurchaseHistory(res.data.data)
      } else {
        message.error(res.data.message)
      }
    } catch (error) {}
  }
  useEffect(() => {
    getCouponPurchaseHistory()
  }, [])

  return (
    <>
      <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Coupon Request</Modal.Title>
        </Modal.Header>
        <Container className="p-3">
          <Row>
            <Form noValidate></Form>
            <div className="row">
              <div className="col-md-6">
                <Form.Group as={Col} controlId="formName">
                  <Form.Label>Coupon</Form.Label>
                  <Form.Control
                    size="sm"
                    type="text"
                    placeholder="P Coupon"
                    disabled
                    value={'P Coupon'}
                    required
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group as={Col} controlId="formName">
                  <Form.Label>Retailer Id</Form.Label>
                  <Form.Control
                    size="sm"
                    type="text"
                    placeholder="Retailer ID"
                    disabled
                    value={user && user.uniqueId}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="row mt-2">
              <Form.Group as={Col} controlId="formName">
                <Form.Label>User Name</Form.Label>
                <Form.Control
                  size="sm"
                  type="text"
                  placeholder="User Name"
                  disabled
                  value={user && user.name}
                  required
                />
              </Form.Group>
              <div className="col-md-4">
                <Form.Group as={Col} controlId="formName">
                  <Form.Label>Coupon Quantity</Form.Label>
                  <Form.Control
                    size="sm"
                    type="number"
                    placeholder="Enter Coupon quantity"
                    onChange={(e) => {
                      handleInputChange(e);
                      
                    }}
                    value={couponQty}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="row mt-2">
              <div className="col-md-6">
                <Form.Group as={Col} controlId="formName">
                  <Form.Label>Single Coupon Price</Form.Label>
                  <Form.Control
                    size="sm"
                    type="number"
                    placeholder="Enter Coupon quantity"
                    value={user && user.couponPrice}
                    disabled
                    required
                  />
                </Form.Group>
              </div>
              <div className="col-md-6">
                <Form.Group as={Col} controlId="formName">
                  <Form.Label>Total Amount</Form.Label>
                  <Form.Control
                    size="sm"
                    type="number"
                    placeholder="Total Amount"
                    value={totalAmount}
                    disabled
                    required
                  />
                </Form.Group>
              </div>
            </div>
          </Row>
        </Container>
        <div className="m-4">
          <Container className="d-flex justify-content-end">
            <Button className="myCustomBtn" onClick={handlePurchase}>
              Purchase
            </Button>
          </Container>
        </div>
      </Modal>
      <Container fluid className='mb-3'>
        <div className="content-header mt-1">
          <div className="row mb-2">
            <div className="col-sm-6">
            </div>
            <div className="col-sm-6">
              <div className="d-grid gap-2 d-md-flex justify-content-md-end ">
                <Button
                  className="myCustomBtn"
                  onClick={() => {
                    handleShow()
                  }}
                >
                  <i className="fas fa-plus" /> Purchase Coupon
                </Button>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-md-12">
            <div className="card">
              <div className="card-header">
                <div className="row">
                  <div className="col-md-8 col-lg-8 col-xl-8">
                    <h3 className="card-title">Coupons</h3>
                  </div>
                  <div className=" col-md-2 col-lg-2 col-xl-2"></div>
                  {/* <div className="col-md-2 col-lg-2 col-xl-2">
                    <div className="card-tools">
                      <div className="input-group input-group-sm">
                        <input
                          type="text"
                          name="table_search"
                          className="form-control float-right search"
                          placeholder="Search by quantity"
                        />
                        <div className="input-group-append">
                          <button type="submit" className="btn btn-default">
                            <i className="fas fa-search" />
                          </button>
                        </div>
                      </div>
                    </div>
                  </div> */}
                </div>
              </div>
              <div className="card-body table-responsive p-0" style={{ height: 450 }}>
                <table className="table table-head-fixed text-nowrap">
                  <thead>
                    <tr>
                      <th className="text-center">SN</th>
                      <th className="text-center">Date &amp; Time</th>
                      <th className="text-center">Req By</th>
                      <th className="text-center">Retailer Id</th>
                      <th className="text-center">PSA Id</th>
                      <th className="text-center">Qty</th>
                      <th className="text-center">Price</th>
                      <th className="text-center">Single Coupon Price</th>
                      <th className="text-center">Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {couponPurchaseHistory && couponPurchaseHistory.length > 0 ? (
                      couponPurchaseHistory.map((item, idx) => (
                        <tr key={idx}>
                          <td className="text-center">{idx + 1}</td>
                          <td className="text-center">{formattedDate(item.purchaseDate)}</td>
                          <td className="text-center">{item.retailerId.name}</td>
                          <td className="text-center">{item.retailerId.uniqueId}</td>
                          <td className="text-center">
                            {item.retailerId.psaId ? item.retailerId.psaId : 'N/A'}
                          </td>
                          <td className="text-center">{item.quantity}</td>
                          <td className="text-center">{item.totalPrice}</td>
                          <td className="text-center">{user && user.couponPrice}</td>
                          <td className="text-center">
                            <StatusBadge status={item.status} />
                          </td>
                        </tr>
                      ))
                    ) : (
                      <tr>
                        <td colSpan={9} className="text-center text-danger">
                          No data found
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </Container>
    </>
  )
}

export default Index
